main {
  flex: 1;

  p.description {
    margin-top: .2em;
  }
}

@media (min-width: $medium) {
  main {
    &.full-height {
      min-height: calc(100vh - #{$header-height-medium});
    }
  }
}

@media (min-width: $large) {
  main {
    &.full-height {
      min-height: calc(100vh - #{$header-height-large});
    }
  }
}